.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  font-family: Arial, sans-serif;
}

#search-box {
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-bottom: 20px;
  padding: 10px;
  font-size: 16px;
}

.country-list ul {
  padding: 0;
  list-style: none;
}

.country-list li {
  cursor: pointer;
  border: 1px solid #eee;
  margin-bottom: 5px;
  padding: 10px;
}

.country-card {
  text-align: center;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 20px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.flag {
  max-width: 100%;
  height: auto;
  margin-bottom: 10px;
}

.country-name {
  margin: 10px 0;
  font-size: 24px;
}

.loader, .error {
  margin-top: 10px;
  display: none;
}

